$(function()
		{
			var cityname = $( "#cityname" ).val();
            $( "#cityname" ).keyup(function() {
			  $('.selectfrom').val('');
			});
			$( "#cityname" ).autocomplete({
			source: APIURL+'/city/citylistajax',
			minlength: 1,
			select: function( event, ui ){
				
			 $('.selectfrom').val('dropdown');
			}
			});
			$( "#citynamemodel" ).autocomplete({
			source: APIURL+'/city/citylistajax',
			minlength: 1,
			select: function( event, ui ){
				
			 $('.selectfrom').val('dropdown');
			}
			});
			   //for keywords//
			// $( "#categories" ).autocomplete({
			// source: APIURL+'/category/keywordajax?city='+cityname,
			// minlength: 1,
			// select: function( event, ui ) {
		
			// }
			// });
			$(".input_vmobile").hide();
			$(".input_v").hide();
			$("#categoriesmobile").keyup(function(){
			var searchOption = $(this).val();
			var htmlContent = '';
			if(searchOption.length > 2){
			$.ajax({
			type: "GET",
			contentType: "application/json; charset=utf-8",
			url: APIURL+'/category/keywordajax?term='+searchOption,
			dataType: "json",
			success: function (data) {
			if(data.length > 0 ){
			$.each(data, function(index, element) {
				
				if(element.type==1)
				{
					htmlContent+='<li  id='+element.label+' rel='+element.url+'><a href='+SITE_URL+'event/'+element.url+'/'+element.label+'>'+element.value+'</a></li>';
				}
				else
				{
					htmlContent+='<li class="makeeventurl" id='+element.label+' >'+element.value+'</li>';
				}
				
			});
			$(".input_vmobile #userSuggestions").html(htmlContent);
			$(".input_vmobile").show();
			}
			},
			});
			}                              
			});

			$("#categories").keyup(function(){
			var searchOption = $(this).val();
			var htmlContent = '';
			if(searchOption.length > 2){
			$.ajax({
			type: "GET",
			contentType: "application/json; charset=utf-8",
			url: APIURL+'/category/keywordajax?term='+searchOption,
			dataType: "json",
			success: function (data) {
			if(data.length > 0 ){
			$.each(data, function(index, element) {
				
				if(element.type==1)
				{
					htmlContent+='<li  id='+element.label+' rel='+element.url+'><a href='+SITE_URL+'event/'+element.url+'/'+element.label+'>'+element.value+'</a></li>';
				}
				else
				{
					htmlContent+='<li class="makeeventurl" id='+element.label+' >'+element.value+'</li>';
				}
				
			});
			$(".input_v #userSuggestions").html(htmlContent);
			$(".input_v").show();
			}
			},
			});
			}                              
			});
			
			$( "#autocomplete" ).autocomplete({
			  source: APIURL+'/city/citylistajax',
			});


		});
